'use strict';

import mongoose from 'mongoose';
var Schema = mongoose.Schema;

var PurchaseSchema = new Schema({
  buyer: {type: Schema.Types.ObjectId, ref: 'User'},
  project: {type: Schema.Types.ObjectId, ref: 'Project'},
  loanReq: String,
  propertyType: String,

   // Bank details
  bank: {bankCode: String, 
         branchCode: String,
         accountNo: String,
         accountType: String,
         since: String,
         isPrimary: String,
         isMicrcode: String
        },

  // Reference
  reference: {name: String,
              relation: String,
              address1: String,
              address2: String,
              address3: String,
              city: String,
              country: String, 
              state: String,
              pincode: String,
              email: String,
              mobile: String,
              phone: String,
            }
});

export default mongoose.model('Purchase', PurchaseSchema);
